<template>
  <div cssClass="services" class="container">
    <div
      v-if="!loading && items.length === 0"
      class="d-flex services-empty justify-center"
    >
      <div class="align-self-center">
        <btn-secondary 
          text="Cadastrar Serviço"
          icon="add"
          @clicked="addService"
        />
      </div>
    </div>
    <div v-if="items.length > 0">
      <btn-secondary 
        text="Cadastrar"
        icon="add"
        cssClass="py-2"
        class="mt-8 mt-md-4"
        :block="false"
        @clicked="addService"
      />
      <dashboard-table
        class="mt-8"
        :headers="headers"
        :items="items"
        :loading="loading"
        :page="currentPage"
        :qtdPages="qtdPages"
        title="Serviços"
        @changePage="changePage"
        @edit="editService"
        @remove="removeService"
      />
    </div>

    <create-service-modal
      ref="createServiceModal"
      @onSuccess="onSuccessCreateModal"
      @onError="onErrorModal"
    />
    <edit-service-modal
      ref="editServiceModal" 
      @onSuccess="onSuccessUpdateModal"
      @onError="onErrorModal"
    />
    <confirm-dialog
      ref="removeServiceModal"
      title="Deseja realmente remover o serviço?"
      :description="dialogDescription"
      :danger="true"
      @confirm="confirmRemove"
    />
    <help-fab />
    <overlay ref="overlay" />
  </div>
</template>

<script>
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import CreateServiceModal from '@/components/admin/service/Create'
import ConfirmDialog from '@/components/globais/dialogs/confirm/Confirm'
import DashboardTable from '@/components/admin/dashboard/DashboardTable'
import EditServiceModal from '@/components/admin/service/Edit'
import HelpFab from '@/components/admin/HelpFab';
import Overlay from '@/components/globais/overlay/Overlay'
import BarberMixin from '@/mixins/BarberMixin'
import BarbershopServiceMixin from '@/mixins/BarbershopServiceMixin'
import OverlayMixin from '@/mixins/OverlayMixin'
import ServiceMixin from '@/mixins/ServiceMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import TextMixin from '@/mixins/TextMixin'

export default {
  name: 'Service',
  components: { BtnSecondary, ConfirmDialog, CreateServiceModal, DashboardTable, EditServiceModal, HelpFab, Overlay },
  mixins: [ BarberMixin, BarbershopServiceMixin, OverlayMixin, ServiceMixin, SnackbarMixin, TextMixin ],
  data: () => ({
    headers: [
      { text: 'Nome', value: 'name', class: 'text-left' },
      { text: 'Tipo', value: 'service_type', class: 'text-center' },
      { text: 'Valor', value: 'price', class: 'text-center', type: 'money' },
      { text: 'Duração', value: 'duration', class: 'text-center' },
      { text: '#', value: 'actions', type: 'action', class: 'text-center' }
    ],
    items: [],
    loading: false,
    dialogDescription: null,
    id: null
  }), 
  computed: {
    showContent () {
      return this.barber.barbershop_id
    }
  },
  mounted () {
    this.showOverlay()
    if (this.barber.barbershop_id) {
      this.loading = true
      this.getServices()
    }
  },
  methods: {
    addService () {
      this.$refs.createServiceModal.showDialog()
    },
    changePage (page) {
      this.currentPage = page
      this.getServices()
    },
    confirmRemove () {
      this.destroyService(this.id)
    },
    editService (service) {
      this.$refs.editServiceModal.showEditDialog(service)
    },
    formatListItem (item) {
      let durationTime = item.duration_time
      if (durationTime) {
        durationTime = durationTime.split(':')
        durationTime = `${durationTime[0]}:${durationTime[1]}`
      }
      let data = item
      data.name = item.name
      data.service_type = this.getServiceType(item.service_type_id),
      data.duration = durationTime
      data.price = item.price
      data.actions = [
        { icon: 'edit', text: 'Editar', class: 'success text-none', action: 'edit' }, 
        { icon: 'delete', text: 'Remover', class: 'danger text-none', action: 'remove' }
      ]
      return data
    },
    getServiceTime (durationTime) {
      durationTime = durationTime.split(':')
      return `${durationTime[0]}:${durationTime[1]}`
    },
    getServiceType (service_type_id) {
      let service = this.servicesTypes.find(i => i.value == service_type_id)
      return service.text
    },
    onDestroyServiceSuccess (response) {
      this.showSnackbar(response)
      let index = this.items.findIndex(i => i.id == this.id)
      if (index != -1) this.items.splice(index, 1)
    },
    onDestroyServiceError (error) {
      this.showSnackbarErro(error)
    },
    onGetServicesByBarbershopIdError (error) {
      this.loading = false
      this.hideOverlay('')
      this.showSnackbarErro(error)
    },
    onGetServicesByBarbershopIdSuccess (response) {
      this.hideOverlay()
      this.loading = false
      this.currentPage = response.current_page
      this.qtdPages = response.last_page
      this.items = response.data.map(item => this.formatListItem(item))
    },
    onSuccessCreateModal () {
      this.showSnackbar('Serviço salvo com sucesso!')
      this.getServices()
    },
    onErrorModal (error) {
      this.showSnackbarErro(error)
    },
    onSuccessUpdateModal (response) {
      this.showSnackbar('Serviço salvo com sucesso!')
      let index = this.items.findIndex(i => i.id == response.id)
      if (index >= 0) {
        const service = this.formatListItem(response)
        this.items.splice(index, 1, service)
      }
    },
    removeService (service) {
      this.id = service.id
      this.dialogDescription = service.name
      this.$refs.removeServiceModal.showDialog()
    },
    getServices () {
      this.loading = true
      this.getServicesByBarbershopId(this.barber.barbershop_id, true)
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';

.services { 
 .services-empty { height: calc(100vh - 162px); }
  .service-table { width: 100%; overflow: hidden; overflow-x: auto;
    table { width: 100%;
      thead tr { border-bottom: 2px solid $primaryLight; }
      tr { display: flex; align-items: center; border-bottom: 1px solid $muted;
        td, th { text-align: center; width: -webkit-fill-available;
          &:first-child { text-align: left; }
          &:last-child { width: 440px; }
        }
      }
    }
  }
}
</style>